import { hashPassword } from '@/shared/Hash'
import { required, minLength, sameAs } from 'vuelidate/lib/validators'
export default {
    name: 'change-password',
    data() {
        return {
            employeeId: this.$store.getters.getUid,
            errorMessage: '',
            password: {
                new: '',
                confirmNew: ''
            },
            username: this.$store.getters.getUsername,
        }
    },
    validations: {
        password: {
            new: { 
                required,
                minLength: minLength(8)
            },
            confirmNew: { 
                required,
                minLength: minLength(8),
                sameAs: sameAs(function() { return this.password.new })
            }
        }
    },
    methods: {
        handleSubmit() {
            this.$v.$touch()
            if (this.$v.$invalid) {
                this.error = 'Please recheck the form.'
            } else {
                console.log('Hello')
                this.submitPassword()
            }
        },
        submitPassword() {
            this.$bvModal.show('modal-loading')
            setTimeout(() => {
                this.$http({
                    method: 'post',
                    url: `/employee/managed-employee/set-password/${this.employeeId}`,
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.getToken
                    },
                    data: {
                        user_pass: hashPassword(this.username, this.password.new).toString()
                    }
                })
                .then(response => {
                    this.$bvModal.hide('modal-loading')
                    this.$router.push('/admin/profile')
                })
                .catch(error => {
                    this.$bvModal.hide('modal-loading')
                    this.errorMessage = error.response.data
                })
            }, 500)
        }
    }
}